.chat {
  display: flex;
  flex-direction: column;
  max-width: 800px;
}

.messageInput {
  & > input[type=text] {
    border-radius: 24px;
    border: 1px thistle solid;
    margin: 2px;
    max-width: 300px;
    padding: 8px 12px;
    outline: none;
  }

}

.message {
  align-self: flex-start;
  border-radius: 24px;
  border: 1px thistle solid;
  margin: 2px;
  max-width: 300px;
  padding: 8px 12px;

  &__sender {
    align-self: flex-end;
  }
}